// 
let codeReg = /^(?![0-9]*$)(?![a-zA-Z]*$)[a-zA-Z0-9]{6,20}$/;
// 
let phoneReg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;

// 
let numberReg = /^\d+$|^\d+[.]?\d+$/;

// 
let passwordReg = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$)([^\u4e00-\u9fa5\s]){6,20}$/;

// 
let contactsReg = /^[\u0391-\uFFE5A-Za-z]+$/;

//
let regId = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;

//
let emailReg = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;

//
let nameReg = /[\u4e00-\u9fa5]/gm;

//QQ
let qqReg = /^[1-9][0-9]{4,}$/;

//
let enReg = /^[a-z]+$/i;

let urlReg = /^http:\/\/[A-Za-z0-9]+\.[A-Za-z0-9]+[\/=\?%\-&_~`@[\]\':+!]*([^<>\"\"])*$/;

//IP
let ipReg = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;

// 
let accessReg = /^[a-z0-9]+$/i;
//
let card_number_reg = /^(\d{16}|\d{19})$/;
// 
let usernameReg=/^[a-zA-Z0-9_-]{4,10}$/;
let FormValidate = (function() {
  function FormValidate() {}
  // From  
  FormValidate.Form = function() {
    return {
      // 
      validateCode(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!codeReg.test(value)) {
          callback(new Error("6-20"));
        } else {
          callback();
        }
      },
      // 
      validateChinese(rule, value, callback) {
        if (value !== "") {
          if (!nameReg.test(value)) {
            callback(new Error(""));
          } else {
            callback();
          }
        } else {
          callback("");
        }
      },
      // 
      validateEnglish(rule, value, callback) {
        if (value !== "") {
          if (!enReg.test(value)) {
            callback(new Error(""));
          } else {
            callback();
          }
        } else {
          callback("");
        }
      },
      // 
      validateNumber(rule, value, callback) {
        if (value !== "") {
          if (!numberReg.test(value)) {
            callback(new Error(""));
          } else {
            callback();
          }
        } else {
          callback("");
        }
      },

      // 
      validatePsdReg(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!passwordReg.test(value)) {
          callback(
            new Error(
              "6-20+"
            )
          );
        } else {
          callback();
        }
      },
            // 
            validateNewPsdReg(rule, value, callback) {
              //console.log(value)
              //console.log(this)
              if (value!== this.numberValidateForm.password) {
                callback(
                  new Error(
                    ""
                  )
                );
              } else {
                callback();
              }
            },

      // 
      validateContacts(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!contactsReg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },

      // 
      validateEmail(rule, value, callback) {
        if (value !== "") {
          if (!emailReg.test(value)) {
            callback(new Error(""));
          } else {
            callback();
          }
        } else {
          callback(new Error(""));
        }
      },

      // 
      validatePhone(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!phoneReg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },

      // 
      ID(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!regId.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },

      // qq
      validateQQ(rule, value, callback) {
        if (!value) {
          return callback(new Error("qq"));
        }
        if (!qqReg.test(value)) {
          callback(new Error("qq"));
        } else {
          callback();
        }
      },
      // ip
      validateIp(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!ipReg.test(value)) {
          callback(new Error("IP"));
        } else {
          callback();
        }
      },
      // url
      validateUrl(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!urlReg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },
      // 
      validateAccess(rule, value, callback) {
        if (!value) {
          return callback(new Error(""));
        }
        if (!accessReg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },
      // 
      validateCardNum(rule, value, callback) {
        if (value !== "") {
          if (!card_number_reg.test(value)) {
            callback(new Error("1619"));
          } else {
            callback();
          }
        } else {
          callback(new Error(""));
        }
      },
      // 
      validateAccountName(rule, value, callback) {
        if (value !== "") {
          if (value.length<2||value.length>10) {
            callback(new Error(""));
          } else {
            callback();
          }
        } else {
          callback(new Error(""));
        }
      },
      // 
      validateBranchkName(rule, value, callback) {
        if (value !== "") {
          if (value.length<5||value.length>30) {
            callback(new Error(""));
          } else {
            callback();
          }
        } else {
          callback(new Error(""));
        }
      },
      // / 4-10
      validateUsername(rule,value,callback){
        if (value !== "") {
          //console.log(usernameReg.test(value))
          if (!usernameReg.test(value)) {
            callback(new Error("4-10"));
          } else {
            callback();
          }
        } else {
          callback(new Error(""));
        }
      },

    };
  };

  return FormValidate;
})();

exports.FormValidate = FormValidate;

